
const request = () => {
  return fetch('https://sasha39612.github.io/online_store/Api/products.json')

  .then(response => {
    if (response.ok) {
      
      return response.json();
    }

    throw `${response.status} - ${response.statusText}`
  });
};

export const getGoodsFromApi = () => {

  return request()
}
